import React from 'react';
import { useSelector } from 'react-redux';

// Components.
import { Dimensions, ScrollView } from 'react-native';
import { List } from 'react-native-paper';
import { DateRequestDialog } from '../components';

// Constants.
import { NO_DATES_MESSAGE } from '../modules/constants/date';

// Styles.
import { utilityStyles } from '../styles';

// Destruct styles.
const { textCenter } = utilityStyles;

export default function Dates() {
  const dates = useSelector(({ dates }) => dates);

  const height = Dimensions.get('window').height - 300;

  return (
    <ScrollView
      style={{ flex: 1 }}
      contentContainerStyle={[{ height }]}
      scrollEnabled={true}
    >
      <List.Section>
        <List.Subheader style={[textCenter]}>
          <DateRequestDialog />
        </List.Subheader>

        {dates.length === 0 && (
          <List.Item
            title={NO_DATES_MESSAGE}
            titleStyle={[textCenter]}
          />
        )}

        {/* {dates.map(date => (
          <List.Item
            key={}
            title={NO_DATES_MESSAGE}
            titleStyle={[textCenter]}
          />
        ))} */}
      </List.Section>
    </ScrollView>
  );
}
